import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import API_BASE_URL from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  data() {
    return {

    };
  },
  state: {
    cartProducts: [],

    userAccessKey: null,

    cartProductsData: [],

    productLoading: false,
    productLoadingFailed: false,

    orderInfo: null,
  },
  mutations: {
    updateCartProducAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((it) => it.productId === productId);

      if (item) {
        item.amount = amount;
      }
    },

    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    },

    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },

    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },

    updateProductLoading(state, bool) {
      state.productLoading = bool;
    },

    updateProductLoadingFailed(state, bool) {
      state.productLoadingFailed = bool;
    },

    syncCartProducts(state) {
      // eslint-disable-next-line
      state.cartProducts = state.cartProductsData.map((item) => {
        return {
          productId: item.product.id,
          amount: item.quantity,
        };
      });
    },

    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },

    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts
        .map((item) => {
          // eslint-disable-next-line
          const product = state.cartProductsData
            .find((p) => p.product.id === item.productId).product;
          return {
            ...item,
            product: {
              ...product,
              image: product.image.file.url,
            },
          };
        });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts
        .reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
    catrTotalProducts(state) {
      const count = state.cartProducts.length;
      return count;
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      // eslint-disable-next-line
      return axios.get(API_BASE_URL + '/api/orders/' + orderId, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      context.commit('updateProductLoading', true);
      context.commit('updateProductLoadingFailed', false);
      // eslint-disable-next-line
      return axios.get(API_BASE_URL + '/api/baskets', {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
        // .catch(context.commit('updateProductLoadingFailed', true))
        .then(context.commit('updateProductLoading', false));
    },
    addProductToCart(context, { productId, amount }) {
      // eslint-disable-next-line
      return axios.post(API_BASE_URL + '/api/baskets/products', {
        productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },

    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProducAmount', { productId, amount });

      if (amount < 1) return;

      // eslint-disable-next-line
      return axios.put(API_BASE_URL + '/api/baskets/products', {
        productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },

    deleteCartProduct(context, productId) {
      context.commit('deleteCartProduct', productId);

      // eslint-disable-next-line
      return axios.delete(API_BASE_URL + '/api/baskets/products', {
        data: { productId },
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
  },
});
