<template>
  <router-link class="header__cart" :to="{ name: 'cart' }" aria-label="Корзина с товарами">
    <svg width="30" height="21" fill="currentColor">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <span class="header__count" aria-label="Количество товаров"
    v-if=" this.$store.state.productLoading">
      ...
    </span><span class="header__count" aria-label="Количество товаров"
     v-if=" this.$store.state.productLoadingFailed">
      Err
    </span>
    <span class="header__count" aria-label="Количество товаров" v-else>
      {{ $store.state.cartProducts.length }}
    </span>
  </router-link>

</template>

<script>
export default {
  data() {
    return {
      productLoading: false,
      productLoadingFailed: false,
    };
  },
  methods: {
    loadProducts() {
      this.productLoading = this.$store.state.productLoading;
      this.productLoadingFailed = this.$store.state.productLoadingFailed;
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
