<template>
  <!-- eslint-disable -->
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ productsCount }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <baseFormText title="ФИО" placeholder="Введите ваше полное имя" v-model="formData.name"
              :error="formError.name" />
            <baseFormText title="Адрес доставки" placeholder="Введите ваш адрес" v-model="formData.address"
              :error="formError.address" />
            <baseFormText title="Телефон" placeholder="Введите ваш телефон" v-model="formData.phone"
              :error="formError.phone" />
            <baseFormText title="Email" placeholder="Введи ваш Email" v-model="formData.email"
              :error="formError.email" />
            <baseFormTextfield title="Комментарий к заказу" placeholder="Ваши пожелания" v-model="formData.comment"
              :error="formError.comment" />

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500" checked="">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>
        <ordersProductList :products="products" />

        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            Похоже произошла ошибка. Попробуйте отправить снова или перезагрузите страницу.
          </p>
        </div>
      </form>
    </section>
  </main>
</template>
<script>
import baseFormText from '@/components/baseFormText.vue';
import baseFormTextfield from '@/components/baseFormTextfield.vue';
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import API_BASE_URL from '@/config';
import ordersProductList from '@/components/ordersProductList.vue';

export default {
  components: { baseFormText, baseFormTextfield, ordersProductList },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
    };
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      productsCount: 'catrTotalProducts',
    }),

  },
  filters: { numberFormat },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      // eslint-disable-next-line
      axios.post(API_BASE_URL + '/api/orders', {

        ...this.formData,
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey,
        },
      })
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((errors) => {
          this.formError = errors.response.data.error.request || {};
          this.formErrorMessage = errors.response.data.error.message || '';
        });
    },
  },
};
</script>
